                _this.businessPlanYear1 = _this.rejectedCust[0].bp1st;
                _this.businessPlanYear2 = _this.rejectedCust[0].bp2nd;
                _this.cc1 = _this.rejectedCust[0].competitionCompanies;
                _this.cc2 = _this.rejectedCust[0].competitionCompanies1;
                _this.cc3 = _this.rejectedCust[0].competitionCompanies2;
                _this.C1sale = _this.rejectedCust[0].c1sale;
                _this.C2sale = _this.rejectedCust[0].c2sale;
