    CustomerRegPage.prototype.loo = function (a, loadingg) {
        var _this = this;
        var imgggg = a;
        var img2 = imgggg.split("base64,");
        var img3 = img2[1];
        // console.log(img2);
        // console.log(img3);
        var url = Enums.APIURL.URL + "SavePng";
        var headers1 = new Headers({
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        });
        var options = new RequestOptions({ headers: headers1 });
        var data = JSON.stringify({
            Name: img3
        });
        return new Promise(function (resolve, reject) {
            _this.http.post(url, data, options)
                .toPromise()
                .then(function (response) {
                console.log('API Response : ', response.json());
                _this.contactattachments = _this.contactattachments.concat(response.json() + ',');
                console.log(_this.contactattachments);
                // setTimeout(() => {
                // }, 3000);
                _this.count = _this.count + 1;
                if (_this.count !== _this.attachments.length) {
                    console.log(_this.count, '==', _this.attachments.length);
                }
                else {
                    loadingg.dismissAll();
                    // This part is Added By Mohsin Ali to disapper submit button while uploading to prevent  from duplicate  entry 
                    // EDITED on : 16-04-2019
                    _this.checksubmit = true;
                    _this.postAttachments(_this.custidx, _this.contactattachments).then(function (xx) {
                        if (xx) {
                            _this.presentToast('Request for new customer registration has been sent to administration.');
                            _this.navCtrl.pop();
                        }
                    });
                    console.log(_this.count, '==', _this.attachments.length, 'done');
                }
            })
                .catch(function (error) {
                loadingg.dismiss();
                console.log('API Error : ', error.status);
                console.log('API Error : ', JSON.stringify(error));
                reject(false);
            });
            // this.uploadImageBytes(a,1);
        });
    };
    CustomerRegPage.prototype.letslook = function () {
        var loadingg = this.loadingCtrl.create({
            content: 'wait please...',
        });
        loadingg.present();
        this.checksubmit = false;
        // This part is Added By Mohsin Ali to disapper submit button while uploading to prevent  from duplicate  entry 
        // EDITED on : 16-04-2019
        for (var _i = 0, _a = this.attachments; _i < _a.length; _i++) {
            var a = _a[_i];
            this.loo(a, loadingg);
        }
    };
