    CustomerRegPage.prototype.postAttachments = function (custIDX, attaments) {
        var _this = this;
        var loader = this.loadingCtrl.create({
            content: "Please wait...",
            duration: 3000
        });
        loader.present();
        return new Promise(function (resol) {
            var attachment = attaments;
            var headers1 = new Headers({
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            });
            var options = new RequestOptions({ headers: headers1 });
            var data;
            // if(this.attach2 === "-"){
            data = JSON.stringify({
                CustomerIdx: custIDX,
                Attachments: attachment,
            });
            console.log("Customer Complain JsonFormat................");
            console.log(data);
            // }
            return new Promise(function (resolve, reject) {
                _this.http.post(Enums.APIURL.URL + 'savecustomerattachment', data, options)
                    .toPromise()
                    .then(function (response) {
                    console.log('API Response : ', response.json());
                    loader.dismissAll();
                    resol(true);
                    resolve(response.json());
                })
                    .catch(function (error) {
                    loader.dismissAll();
                    console.error('API Error : ', error.status);
                    console.error('API Error : ', JSON.stringify(error));
                    reject(error.json());
                });
            });
        });
    };
