    CustomerRegPage.prototype.map = function () {
        var _this = this;
        cordova.plugins.diagnostic.isLocationAvailable(function (available) {
            console.log("Location is " + (available ? "available" : "not available"));
            if (!available) {
                _this.alert();
            }
            else {
                _this.navCtrl.push('MapPage');
            }
        }, function (error) {
            console.error("The following error occurred: " + error);
        });
    };
    CustomerRegPage.prototype.ionViewWillEnter = function () {
        var _this = this;
        var loader = this.loadingCtrl.create({
            content: "Please wait...",
            duration: 3000
        });
        loader.present();
        this.http.get(Enums.APIURL.URL + 'setPasswordPolicy').map(function (res) { return res.json(); }).subscribe(function (data) {
            // debugger;
            var obj = JSON.parse(data);
            // alert(obj[0].userName);
            loader.dismiss();
            _this.passwordPolicy = obj[0].RegularExp;
            console.log("passPolicy", _this.passwordPolicy);
            //  this.loading.dismiss();
            // resolve(true);
        }, function (err) {
            console.log(err);
            loader.dismiss();
            // resolve(false);
            // alert(err);
        });
        this.nativeStorage.getItem('loc')
            .then(function (data) {
            _this.latitude = data.lat;
            _this.longitude = data.lng;
            console.log(data.lat, data.lng);
        }, function (error) { return console.error(error); });
    };
    CustomerRegPage.prototype.isValid = function (pass, cpass) {
        console.log(pass, "___", cpass);
        if (pass === cpass) {
            var regexx = this.passwordPolicy;
            var str = regexx.toString();
            var parts = /\/(.*)\/(.*)/.exec(str);
            var restoredRegex = new RegExp(parts[1], parts[2]);
            // console.log(this.passwordPolicy);
            if (restoredRegex.test(pass)) {
                return true;
            }
            else {
                this.custAlert.presentAlert('Bad Password', 'Password must contains special characters and not be less than 8 characters.');
                return false;
            }
        }
        else {
            this.custAlert.presentAlert('Wrong Password', 'Password not matched with confrim Password field.');
            return false;
        }
    };
